import {ServiceProviders} from "./Enums/EServiceProviders";
import {IAuth, IServiceOptions} from "./Interfaces/IServiceOprtions";

export class Auth {
	get user(): string {
		return this._user;
	}

	set user(value: string) {
		this._user = value;
	}

	get pass(): string {
		return this._pass;
	}

	set pass(value: string) {
		this._pass = value;
	}

	private _user: string;
	private _pass: string;

	constructor(auth:IAuth){
		this.user = auth.user;
		this.pass = auth.pass;
	}
}

export class ServiceOptions{
	set service(value: string) {
		this._service = value;
	}

	get auth(): Auth {
		return this._auth;
	}

	set auth(value: Auth) {
		this._auth = value;
	}

	private _service: string;
	private _auth: Auth;

	constructor(opts:IServiceOptions){
		this.auth = new Auth(opts.auth);
		this.service = ServiceProviders[opts.service];
	}
}